import React, { useState, useEffect } from 'react';
import Editor from '@monaco-editor/react';

interface SpecEditorProps {
  spec: any;
  onChange: (spec: any) => void;
}

export const SpecEditor: React.FC<SpecEditorProps> = ({ spec, onChange }) => {
  const [specCode, setSpecCode] = useState<string>('');
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (spec) {
      setSpecCode(JSON.stringify(spec, null, 2));
    }
  }, [spec]);

  const handleEditorChange = (value: string | undefined) => {
    if (value !== undefined) {
      setSpecCode(value);
      
      try {
        const parsedSpec = JSON.parse(value);
        onChange(parsedSpec);
        setError(null);
      } catch (err) {
        setError('JSON ');
      }
    }
  };

  const copyToClipboard = async () => {
    try {
      await navigator.clipboard.writeText(specCode);
      // TODO: 
      console.log('');
    } catch (err) {
      console.error(':', err);
      // 
      alert('');
    }
  };

  return (
    <div className="spec-editor-container">
      <div className="spec-editor-header">
        <h3> </h3>
        <button 
          className="copy-btn"
          onClick={copyToClipboard}
          title=""
        >
           
        </button>
      </div>
      
      {error && (
        <div className="error-message">
           {error}
        </div>
      )}
      
      <div className="spec-editor-wrapper">
        <Editor
          height="400px"
          defaultLanguage="json"
          value={specCode}
          onChange={handleEditorChange}
          options={{
            minimap: { enabled: false },
            fontSize: 12,
            lineNumbers: 'on',
            roundedSelection: false,
            scrollBeyondLastLine: false,
            automaticLayout: true,
            tabSize: 2,
            wordWrap: 'on',
            theme: 'vs-dark',
            formatOnPaste: true,
            formatOnType: true
          }}
        />
      </div>
      
      <div className="spec-editor-footer">
        <span className="hint"> JSON</span>
      </div>
    </div>
  );
};