import React, { useEffect, useRef } from 'react';

interface ChartRendererProps {
  spec: any;
  error: string | null;
  isLoading: boolean;
}

export const ChartRenderer: React.FC<ChartRendererProps> = ({ spec, error, isLoading }) => {
  const chartRef = useRef<HTMLDivElement>(null);
  const chartInstanceRef = useRef<any>(null);

  useEffect(() => {
    if (spec && chartRef.current) {
      renderChart();
    }
    
    return () => {
      if (chartInstanceRef.current) {
        chartInstanceRef.current.release();
        chartInstanceRef.current = null;
      }
    };
  }, [spec]);

  const renderChart = async () => {
    if (!chartRef.current || !spec) return;

    try {
      // 
      if (chartInstanceRef.current) {
        chartInstanceRef.current.release();
        chartInstanceRef.current = null;
      }

      chartRef.current.innerHTML = '';

      // VChart
      if (typeof window === 'undefined' || !(window as any).VChart) {
        throw new Error('VChart ');
      }

      // 
      const vchartSpec = {
        ...spec,
        width: 800,
        height: 400,
        title: {
          text: "VizSeed ",
          fontSize: 16,
          fontWeight: "bold"
        }
      };

      // VChart
      const VChart = (window as any).VChart.VChart;
      chartInstanceRef.current = new VChart(vchartSpec, { dom: chartRef.current });
      
      await chartInstanceRef.current.renderAsync();
      console.log(' VChart');
      
    } catch (err) {
      console.error(' VChart:', err);
      if (chartRef.current) {
        chartRef.current.innerHTML = `
          <div style="color: red; text-align: center; padding: 50px; font-size: 14px;">
            <h3></h3>
            <p>${err instanceof Error ? err.message : ''}</p>
          </div>`;
      }
    }
  };

  if (isLoading) {
    return (
      <div className="chart-container">
        <div className="chart-header">
          <h3> </h3>
        </div>
        <div className="chart-loading">
          <div className="spinner"></div>
          <p>...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="chart-container">
        <div className="chart-header">
          <h3> </h3>
        </div>
        <div className="chart-error">
          <h3> </h3>
          <p>{error}</p>
        </div>
      </div>
    );
  }

  if (!spec) {
    return (
      <div className="chart-container">
        <div className="chart-header">
          <h3> </h3>
        </div>
        <div className="chart-placeholder">
          <h3> </h3>
          <p>VizSeed</p>
        </div>
      </div>
    );
  }

  return (
    <div className="chart-container">
      <div className="chart-header">
        <h3> </h3>
        <div className="chart-info">
          : {spec.type?.toUpperCase() || 'Unknown'}
        </div>
      </div>
      <div className="chart-wrapper">
        <div ref={chartRef} className="chart"></div>
      </div>
    </div>
  );
};